import React, { useCallback, useState } from 'react'
import styled from 'styled-components'
import { Button, Text } from '../../uikit-foodcourt'
import { AlertTriangle } from 'react-feather'
import Modal from '../Modal'
import { AutoRow, RowBetween } from '../Row'
import { AutoColumn } from '../Column'

const WarningContainer = styled.div`
	max-width: 420px;
	width: 100%;
	padding: 1rem;
	background: rgba(242, 150, 2, 0.05);
	border: 1px solid #f3841e;
	border-radius: 20px;
	overflow: auto;
`

const StyledWarningIcon = styled(AlertTriangle)`
	stroke: ${({ theme }) => theme.colors.binance};
`

export default function EndServiceModal({
	isOpen,
	onConfirm,
}: {
	isOpen: boolean
	onConfirm: () => void
}) {
	const [understandChecked, setUnderstandChecked] = useState(false)
	const toggleUnderstand = useCallback(() => setUnderstandChecked((uc) => !uc), [])

	const handleDismiss = useCallback(() => null, [])
	return (
		<Modal isOpen={isOpen} onDismiss={handleDismiss} maxHeight={90}>
			<WarningContainer className="token-warning-container">
				<AutoColumn gap="lg">
					<AutoRow gap="6px">
						<StyledWarningIcon />
						<Text>Notice for ending services on BKC (Bitkub chain)</Text>
					</AutoRow>
					<>
						<Text>
							<strong>Dear respected investors,</strong><br />
							<div style={{ marginTop: 8 }}>First of all, Your fund are safe, dont FUD.</div>
							<div style={{ marginTop: 8 }}>Foodcourt has decided to operate mainly on BSC (Binance Smart Chain). We are an international team based outside thailand territory, so we dont have the required resources to operate on BKC.</div>
							<div style={{ marginTop: 8 }}>Currently, we found out BKC already has fully functional dex that use foodcourt router which is</div>
						</Text>
						<Text>
							<div><a href="https://exchange.tuktuk.finance/#/swap" style={{ whiteSpace: "pre-line", textDecoration: "underline" }}>https://exchange.tuktuk.finance/#/swap</a></div>
						</Text>
						<Text>
							Or if you want to run website on localhost , we open source kubx.foodcourt.exchange at
						</Text>
						<Text>
							<div><a href="https://github.com/foodcourtofficial/bkc-decommissioned" style={{ whiteSpace: "pre-line", textDecoration: "underline" }}>https://github.com/foodcourtofficial/bkc-decommissioned</a></div>
						</Text>
						<Text>
							<strong>Foodcourt will stop operating website kubx.foodcourt.exchange on 19 June 2021 3:00 am (UTC)</strong>
						</Text>
					</>
					<RowBetween>
						<div>
							<label htmlFor="understand-closing" style={{ cursor: 'pointer', userSelect: 'none' }}>
								<input
									id="understand-closing"
									type="checkbox"
									className="understand-checkbox"
									checked={understandChecked}
									onChange={toggleUnderstand}
								/>{' '}
								<Text as="span">I understand</Text>
							</label>
						</div>
						<Button
							id="confirm-moonMaiKubWarning"
							disabled={!understandChecked}
							variant="danger"
							style={{ width: '140px' }}
							onClick={() => {
								setUnderstandChecked(false)
								onConfirm()
							}}
						>
							Continue
						</Button>
					</RowBetween>
				</AutoColumn>
			</WarningContainer>
		</Modal>
	)
}
