const WETH_ONLY: ChainTokenList = {
	[ChainId.MAINNET]: [WETH[ChainId.MAINNET]],
	[ChainId.BSCTESTNET]: [WETH[ChainId.BSCTESTNET]],
}

function parseCenterTokens(tokens) {
	for (const chainId in tokens) {
		const chainIdForce: any = parseInt(chainId);
		tokens[chainId] = tokens[chainId].map(x => new Token(chainIdForce, x.address, x.decimals, x.symbol, x.name))
	}
	// console.log(tokens)
	return tokens;
}

const CENTER_TOKENS: ChainTokenList = window.localStorage.getItem('CENTER_TOKENS')
	? parseCenterTokens(JSON.parse(window.localStorage.getItem('CENTER_TOKENS')))
	: {
			[ChainId.MAINNET]: [
				WETH[ChainId.MAINNET],
				KDOLLY[ChainId.MAINNET],
				DAI[ChainId.MAINNET],
				UST[ChainId.MAINNET],
			],
			[ChainId.BSCTESTNET]: [
				WETH[ChainId.BSCTESTNET],
				KDOLLY[ChainId.BSCTESTNET],
				DAI[ChainId.BSCTESTNET],
				UST[ChainId.BSCTESTNET],
			],
	  }

// used to construct intermediary pairs for trading
export const BASES_TO_CHECK_TRADES_AGAINST: ChainTokenList = {
	...CENTER_TOKENS,
}

/**
 * Some tokens can only be swapped via certain pairs, so we override the list of bases that are considered for these
 * tokens.
 */
export const CUSTOM_BASES: { [chainId in ChainId]?: { [tokenAddress: string]: Token[] } } = {
	[ChainId.MAINNET]: {},
}

// used for display in the default list when adding liquidity
export const SUGGESTED_BASES: ChainTokenList = {
	...CENTER_TOKENS,
}

// used to construct the list of all pairs we consider by default in the frontend
export const BASES_TO_TRACK_LIQUIDITY_FOR: ChainTokenList = {
	...CENTER_TOKENS,
}

